<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'></span>
                    <span class="sign-in"></span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <div class='rule-box'>
            <p>&nbsp;<span>{{ applyNode }}</span>&nbsp;</p>
            <p>&nbsp;<span>{{ waitNode }}</span>&nbsp;</p>
          </div>
      </div>

      <div class='buy-effect'>
        <div class='buy-effect-list'>
          <span></span>
          <input type="checkbox" name='type-buy' value="alipay" checked="checked" v-model='picked'>
          <input type="checkbox" name='type-buy' value="wechat"  v-model='picked'>
          <input type="checkbox" name='type-buy' value="bank"  v-model='picked'>
        </div>
        <div class='buy-list'>
          <span></span>
          <input type="number" name='' placeholder="" v-model='buyNum'>      
        </div>
        <div class='buy-list'>
          <span></span>
          <input type="password" name=''placeholder="" v-model='repassword'>      
        </div>

        <p></p>

        <button @click='goRecord'>   </button>
      </div>
    
      <div class='record-list'>

        <p v-if="PurchaseList == ''"></p>

        <div class='sell-content' v-else>
          <ul class='sell-title'>
            <li class='width-four'>ID</li>
            <li class='width-one'></li>
            <li class='width-two'></li>
            <li class='width-three'></li>
          </ul>
          <ul class='sell-list' v-for='(item,index) in PurchaseList' :key='item.index'>
            <li class='width-four'>{{ item.id }}</li>
            <li class='width-one'>{{ item.amount }}</li>
            <li class='width-two'>{{ item.time1 }}</li>
            <li class='width-three' v-if='item.status == 1'></li>
            <li class='width-three' v-else-if='item.status == 0'></li>
            <li class='width-three' v-else-if='item.status == 2'></li>
            <li class='width-three' v-else-if='item.status == 3'></li>
            <li class='width-three' v-else-if='item.status == 4'></li>
            <li class='width-three' v-else></li>
          </ul>
        </div>

      </div>
      


    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'sellNode',
    component:{
    
    },
    data () {
        return {
           paytype:'',//
           applyNode:'0',//
           waitNode:'0',//
           buyNum:'',//
           repassword:'',//
           picked: ['alipay'],//
           loading: '',//loading
           PurchaseList:[],//

        }
    },
    methods: {
        goBack(){
          this.$router.go(-1)
        },
        getNode(){ //
            axios.post(sell_and_buy_node)
            .then(this.getNodeSucc)
        },
        getNodeSucc(res){
            const data = res.data
            if(data.error == null){
              this.applyNode = data.result.buy
              this.waitNode = data.result.sell
            }
        },
        goRecord(){ //
           if(this.buyNum == ''){
              this.$layer.msg('')
           } else if (this.repassword == '') {
              this.$layer.msg('')
           } else {
              const newData = ({
                'num': this.buyNum,
                'password':this.repassword,
                'paytype': this.picked

              })
              this.purchasenode(newData)
           }
        },
        purchasenode(newData){
          this.loading = layer.load(2, {
                shade: [0.2,'#000'] //0.1
              })
          axios.post(sell_node,newData)
          .then(this.purchasenodeSucc)
          .catch(this.purchasenodeError)
        },
        purchasenodeSucc(res){
          const data = res.data
          if (data.error == null) {
              layer.close(this.loading)
              this.$layer.msg('')
              this.buyNum = ''
              this.repassword = ''
              this.$router.push({name:'home'})
          } else {
            layer.close(this.loading)
            this.buyNum = ''
            this.repassword = ''
            this.$layer.msg(data.error.msg)
          }
        },
        purchasenodeError(error){
            layer.close(this.loading)
        },
        getPurchaseList(){ //
          this.loading = layer.load(2, {
                shade: [0.2,'#000'] //0.1
              })
          axios.post(sell_node_list)
          .then(this.getPurchaseListSucc)
          .catch(this.getPurchaseListError)
        },
        getPurchaseListSucc(res){
          const data = res.data
          if(data.error == null){
            this.PurchaseList = data.result
            layer.close(this.loading)
          }else{
            layer.close(this.loading)
            this.$layer.msg(data.error.msg)
          }
        },
        getPurchaseListError(error){

        },
    },
    mounted () {
        this.getNode()
        this.getPurchaseList()
        
    } 
}
</script>

<style scoped>
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 60%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}

.top-bg-box{
  width: 100%;
  height: 13.6rem;
  background: url(../../assets/home/bg-images.png) no-repeat;
  background-size: 100% 100%;
  padding-top: 5rem;
  position: relative;
}
.rule-box{
  width: 15.727rem;
  padding: 2rem;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 0.5rem;
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.rule-box p{
  color: #fff;
  font-size: 0.7rem;
}

.buy-effect{
  width: 100%;
  height: auto;
  padding: 1rem 2rem;
  margin-top: 1rem;
  background: #fff;
}
.buy-effect-list{
  height: 2rem;
  line-height: 2rem;
  color: #000;
  font-size: 0.7rem;
  border-bottom: 1px solid #E5E5E5;
}
.buy-effect-list span{
  margin-right: 0.5rem;
}
.buy-list{
  height: 2rem;
  line-height: 2rem;
  color: #000;
  font-size: 0.7rem;
  border-bottom: 1px solid #E5E5E5;
}
.buy-list input{
  outline: none;
  border:none;
  height: 2rem;
  border-bottom: 1px solid #E5E5E5;
}
.buy-list input::placeholder{
  color: #ccc;
}
.buy-list span{
  margin-right: 0.5rem;
}
.buy-effect button {
  display: block;
  width: 12.364rem;
  height: 1.818rem;
  background-color: #1075df;
  border-radius: 0.182rem;
  color: #fff;
  font-size: 0.8rem;
  line-height: 1.818rem;
  text-align: center;
  margin: 1rem auto;
  outline: none;
}
.buy-effect p{
  margin-top: 1rem;
  color: red;
}
.record-list{
  width: 100%;
  height: auto;
  background: #fff;
  margin-top: 1rem;
  padding: 1rem 0;
  margin-bottom: 1rem;
}
.record-list p{
  color: red;
  font-size: 0.7rem;
  text-align: center;
}
.sell-content{
  width: 100%;
  height: auto;
  background: #fff;
}
.sell-title{
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  font-size: 0.7rem;
  color: #000;
  text-align: center;
  border-bottom: 1px solid #ccc;
}
.sell-title li{
  float: left;
}
.width-one{
  width: 25%;
}
.width-two{
  width: 35%;
}
.width-three{
  width: 20%;
}
.width-four{
  width: 20%;
}

.sell-list{
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  font-size: 0.6rem;
  color: #000;
  text-align: center;
  border-bottom: 1px solid #ccc;
}
.sell-list li{
  float: left;
}
</style>


