<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'></span>
                    <span class="sign-in"></span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <div class='setup-list'>
            <p></p>
            <input type="text" placeholder="" v-model='name'>
          </div>
          <div class='setup-list'>
            <p></p>
            <input type="text" placeholder="" v-model='number'>
          </div>
          <div class="code-list"  v-show='flag'>
            <input type="text" placeholder="" v-model='codephone'>
            <p @click='gainCodeFun'>{{ codeText }}</p>
          </div>
          <div class='setup-list'  v-show='flag'>
            <p></p>
          </div>
          <div class='updata-img' v-if='flag'>
            <img src="../../assets/wallet/erweima-user.png" alt="" id="portrait">
            <input type="file" id="saveImage" name="myphoto" @change="tirggerFile($event)">
          </div>
          <div class='updata-img' v-else>
            <img :src="this.image" alt="" id="portrait">
            <input type="file" id="saveImage" name="myphoto" style="display:none">
          </div>

          <button @click='alipayBtn' v-show='flag'>  </button>
      </div>

     

    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'paySetUp',
    component:{
      
    },
    data () {
        return {
           name:'',//
           number:'',//
           codephone:'',//
           image:'',//
           type:'alipay',//
           codeText: '',
           codeClick: true,  //
           allTime: 120,
           loading: '',//loading
           flag: true,//

        }
    },
    methods: {
      goBack(){
        this.$router.go(-1)
      },
      countdown () {  //    
          this.time = setInterval(this.countdownTime,1000)
          this.countdownTime()
      },
      countdownTime () {
          this.codeText = this.allTime + 's'
          if (this.allTime < 0) {
              clearInterval(this.time)
              this.codeText = ''
              this.codeClick = true
              this.allTime = 120
              this.code=''
              return false
          }
          this.allTime--
      },
     gainCodeFun () { //
          this.codeClick = false
          const newData = ({
              'mobile':this.$store.state.mobile
          })
          this.$axios.post(binding_sms,newData)
              .then(this.gainCodeSucc)
              .catch(this.gainCodeError)
      },
      gainCodeSucc (res) {
          const data = res.data
          if (data.result == true) {
              this.$layer.msg('')
              this.countdown()
          } else {
              this.codeClick = true
              this.$layer.msg(data.error.msg)
          }
      },
      gainCodeError (error) {
          
      },
      alipayBtn(){  //
        let imageAddress = document.getElementById('saveImage').files[0];
        if(this.name == ''){
          this.$layer.msg('')
        } else if (this.number == ''){
          this.$layer.msg('')
        } else if (this.codephone == ''){
          this.$layer.msg('')
        } else if (imageAddress == undefined || imageAddress == ''){
          this.$layer.msg('')
        } else {
          const newData = ({
          "sms":this.codephone,
          "type":this.type,
          "name": this.name,
          "image": this.image,//
          "number": this.number,
          "bankname": '',
          "bankaddress": '',
          })
          this.loading = layer.load(2, {
                shade: [0.2,'#000',],//0.1
                time: 3*1000
              })
          axios.post(pay_save,newData)
          .then(this.alipaySucc)
          .catch(this.alipayError)
        }
        
      },
      alipaySucc (res) {
          const data = res.data
          if (data.error == null) {
              layer.close(this.loading)
              this.$layer.msg('')
              this.$router.push({name: 'wallet'})
          } else {
              layer.close(this.loading)
              this.$layer.msg(data.error.msg)
          }
      },
      alipayError (error) {
           layer.close(this.loading)
      },
      tirggerFile(event){
          var file = event.target.files[0];
          let param = new FormData(); //form
          param.append('file',file);//appendform
          console.log(param.get('file'));
          let config = {
            headers:{'Content-Type':'multipart/form-data'}
          }; //
          console.log(file)
          this.updataImg(param,config)
      },
      updataImg(param,config){
        axios.post(alipay_updata,param,config)
        .then(this.updataImgSucc)
        .catch(this.updataImgError)
      },
      updataImgSucc(res){
        const data = res.data
        if(data.error == null){
          this.image = data.result
        }else{
          this.$layer.msg(data.error.msg)
        }
      },
      updataImgError(error){

      },
      great(){  //
          document.getElementById('saveImage').onchange = function () {
          var imgFile = this.files[0];
          var fr = new FileReader();
          fr.onload = function () {
            document.getElementById('portrait').src = fr.result;
          };
          fr.readAsDataURL(imgFile);
          } 
      },
      getAlipayData(){  //
        this.loading = layer.load(2, {
                shade: [0.2,'#000',],//0.1
                time: 3*1000
              })
        const newData = ({
          "type":this.type
        })
        axios.post(alipay_data,newData)
        .then(this.getAlipayDataSucc)
        .catch(this.getAlipayDataError)
      },
      getAlipayDataSucc(res){
        const data = res.data
        if(data.error == null && data.result == null){
          this.flag = true
          layer.close(this.loading)
        }else if(data.error == null){
          this.name = data.result.realname,
          this.image = url + data.result.image,//
          this.number = data.result.number,
          this.flag = false,
          layer.close(this.loading)
        }else{
          layer.close(this.loading)
          this.flag = true
        }
      },
      getAlipayDataError(error){

      },
    },
    mounted () {
      this.getAlipayData()
      this.great()
    } 
}
</script>

<style scoped>
.top-bar {
  background: url(../../assets/wallet/nav-img.png) no-repeat;
  background-size: 100% 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 66%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}
.top-bar{

}
.top-bg-box{
  width: 100%;
  padding: 5rem 0.5rem 0rem 0.5rem;
}
.setup-list{
  height: 2rem;
  padding-left: 1rem;
  margin-bottom: 0.5rem;
  background:#fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.setup-list p{
  color: #828282;
  font-size: 0.8rem;
}
.setup-list input{
  width: 10rem;
  height: 2rem;
  text-align: right;
  font-size: 0.7rem;
  outline: none;
  padding-right: 1rem;
}
.code-list{
  height: 2rem;
  padding-left: 1rem;
  margin-bottom: 0.5rem;
  background:#fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.code-list input{
  width: 10rem;
  height: 2rem;
  text-align: left;
  font-size: 0.8rem;
  outline: none;
  /*padding-right: 1rem;*/
}
.code-list input:placeholder{
  font-size: 0.8rem;
}
.code-list p{
  color: #1D7AE0;
  font-size: 0.7rem;
  padding-right: 1rem;
}
.updata-img{
  width: 10rem;
  height: 10rem;
  padding: 0.5rem;
  background: #fff;
  border: 1px solid #ccc;
  margin: 2rem auto;
  position: relative;
}
.updata-img img{
  position: absolute;
  top: 0;
  left: 0;
  width: 10rem;
  height: 10rem;
}
.top-bg-box button{
  width: 13rem;
  height: 2rem;
  display: block;
  background: #1074E3;
  border-radius: 0.5rem;
  outline: none;
  margin: 2rem auto;
  font-size: 0.8rem;
  color: #fff;
}
#saveImage{
  position: absolute;
  top: 0;
  left: 0;
  width: 10rem;
  height: 10rem;
  opacity: 0;
}
</style>


